@import "../../static/sass/mixins.scss";

.label {
  position: relative;
  display: flex;
}

.iconWrapper {
  position: absolute;
  top: 50%;
  left: 16px;

  display: flex;
  align-items: center;
  justify-content: center;

  transform: translateY(-50%);
}

.input {
  @include inputStyle();
}

.input.inputFilled {
  @include inputFilled();
}

.fakePlaceholder {
  @include fakePlaceHolder();
}

.fakePlaceholder.focus {
  @include placeHolderFocus();
}

.errorMessage {
  @include errorMessage();
}
